nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: #fff;
    padding: 10px;
  }
  
  ul {
    display: flex;
    list-style: none;
  }
  
  li {
    margin-right: 50px;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  .active {
    font-weight: bold;
  }
  